@import url(https://fonts.googleapis.com/css2?family=Jost:wght@400;500&family=Rowdies&display=swap&family=Inconsolata);
* {
  margin: 0;
  font-family: "Jost", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1.5;
}

code {
  font-family: proxima-nova, Helvetica, Arial, sans-serif;
}

body {
  width: 100%;
  margin: 0;
  padding: 0;
  height: 100%;
  overflow-x: hidden;
}

a {
  transition: all 0.2s ease-in-out;
  text-decoration: none;
  font-weight: 700;
  color: inherit;
}

@media screen and (min-width: 601px) {
  /* body {
    padding: 2.5em;
  } */
}


:root {
  --color-blue: rgb(16, 162, 245);
  --color-light-periwinkle: #88ace0;
  --color-dark-periwinkle: rgb(168, 178, 209);

  --font-header: "Rowdies", cursive;
  --font-robot: Inconsolata, monospace;

  --font-size-welecome: 30px;
}

.Project-container {
  padding: 90px 0 380px 0;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  width: 1000px;
  min-width: 1000px;
}

.Project-container:last-of-type {
  margin-bottom: 100px;
}

.Project-img-container {
  position: absolute;
  display: block;
}

.Project-img {
  width: 450px;
  position: absolute;
  border-radius: 4px;
  box-shadow: 0 10px 25px 2px grey;
}

.Project-img--left {
  left: 520px;
}

.Project-board {
  width: 375px;
  padding: 25px 0;
  font-size: 15px;
}

.Project-board--left {
  position: absolute;
  text-align: left;
}

.Project-board--right {
  position: absolute;
  text-align: right;
  left: 850px;
}

.Project-text {
  padding: 10px 0;
  letter-spacing: 0.4px;
}

.Project-header {
  font-size: 20px;
  font-family: var(--font-header);
}

.Project-icon {
  margin-top: 15px;
  cursor: pointer;
}

.Project-icon:hover {
  cursor: pointer;
}

.Language-list {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  padding: 0px;
  margin: 15px 0 10px;
}

.Language-list-elem {
  margin: 3px 10px 3px 0;
  font-family: var(--font-robot);
  font-size: 0.75rem;
  font-weight: 600;
  padding: 2px 10px;
  border-radius: 3px;
  color: #007bff;
  border: 1px solid #007bff;
  background: 0 0;
}

.Header-text {
  font-size: 30px;
  font-family: "Rowdies", cursive;
  letter-spacing: 1px;
}

.Header-container {
  text-align: center;
  padding: 100px 0 50px;
}

.Project-all-container {
  display: block;
  padding: 0 18%;
  margin: 0;
}

.Welcome-section {
  height: 100%;
  width: 100%;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.Welcome-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 0;
}

.Girl-diva {
  height: 700px;
  margin-top: 50px;
  width: auto;
}

.DisplayText {
  font-size: var(--font-size-welecome);
  font-family: var(--font-header);
}

.Welcome-text-container {
  margin-left: 25px;
  width: 360px;
  min-width: 400px;
  pointer-events: none;
  letter-spacing: 2px;
  line-height: 2;
}

/* Mobile */
@media only screen and (max-width: 600px) {
  .Welcome-container {
    flex-direction: column-reverse;
    justify-content: center;
    text-align: center;
    align-items: center;
    margin: 50px 30px 0 0;
  }

  .Girl-diva {
    height: 500px;
    width: auto;
    margin: 30px 0 0 30px;
  }

  .Welcome-text-container {
    margin-left: 40px;
    width: 300px;
    min-width: 300px;
    letter-spacing: 1.25px;
  }

  .DisplayText,
  .Welcome-subtext,
  .Girl-diva {
    margin-top: 30px;
  }

  .Welcome-subtext {
    font-size: 14px;
  }
}

.Footer {
  line-height: 1;
  font-size: 14px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  height: auto;
  min-height: 70px;
  padding: 15px;
}

.Footer-credit {
  font-weight: 600;
  letter-spacing: 0.025em;
  font-family: Inconsolata, monospace;
  padding: 10px;
  text-decoration: none;
}
.Footer-credit:visited {
  color: rgb(168, 178, 209);
}

.Experience-page {
  height: 100%;
  width: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.Tab-container {
  padding: 30px 20% 120px;
  height: 400px;
  display: block;
  align-items: center;
}

.Tab-container ul {
  display: block;
  position: relative;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  z-index: 3;
  padding: 0px;
  margin-bottom: 30px;
  list-style: none;
}

.Tab-container li {
  display: inline-flex;
}

.Tab-button {
  position: relative;
  margin: 0;
  height: 50px;
  width: 200px;
  display: flex;
  align-items: center;
  -webkit-box-align: center;
  padding: 0px 0 2px;
  justify-content: center;
  cursor: pointer;
  font-size: 20px;
  font-family: var(--font-header);
  text-align: left;
  text-decoration: none;
  -webkit-text-decoration-skip: ink;
          text-decoration-skip-ink: auto;
  background-color: transparent;
  white-space: nowrap;
  border: none;
  border-bottom: 2px solid #cfdef3;
  transition: all 0.25s cubic-bezier(0.645, 0.045, 0.355, 1) 0s;
}

.Tab-button:focus {
  outline: none;
}

.Tab-button:hover {
  background-color: #cfdef3;
}

.Tab-button:active {
  outline: none;
  background-color: #cfdef3;
}

.Tab-heading {
  font-size: 20px;
  font-family: var(--font-header);
}

.Place-location {
  padding: 0 0 10px 0;
}

.Place-role {
  font-size: 18px;
}

.Place-location-text {
  color: var(--color-light-periwinkle);
  font-weight: 500;
}

.Place-time {
  color: var(--color-dark-periwinkle);
}

.Icon-container {
  text-align: center;
  justify-content: center;
  display: flex;
  align-items: center;
  padding: 0 0 100px 0;
}

.Icon-container li {
  display: inline-flex;
  padding-right: 40px;
}

.Writing-page-content {
  display: block;
}

.Writing-archive {
  overflow: hidden;
  display: flex;
  padding: 20px 0 30px 0;
}

.Writing-archive div {
  position: absolute;
  width: 20%;
  right: -100%;
  justify-content: space-between;
  -webkit-animation: marquee 50s linear 0s infinite;
          animation: marquee 50s linear 0s infinite;
}

.Writing-archive .line-0 {
  -webkit-animation: marquee 50s linear 0s infinite;
          animation: marquee 50s linear 0s infinite;
}

.Writing-archive .line-1 {
  -webkit-animation: marquee 50s linear -5s infinite;
          animation: marquee 50s linear -5s infinite;
}

.Writing-archive .line-2 {
  -webkit-animation: marquee 50s linear -10s infinite;
          animation: marquee 50s linear -10s infinite;
}

.Writing-archive .line-3 {
  -webkit-animation: marquee 50s linear -15s infinite;
          animation: marquee 50s linear -15s infinite;
}

.Writing-archive .line-4 {
  -webkit-animation: marquee 50s linear -20s infinite;
          animation: marquee 50s linear -20s infinite;
}

.Writing-archive .line-5 {
  -webkit-animation: marquee 50s linear -25s infinite;
          animation: marquee 50s linear -25s infinite;
}

.Writing-archive .line-6 {
  -webkit-animation: marquee 50s linear -30s infinite;
          animation: marquee 50s linear -30s infinite;
}

.Writing-archive .line-7 {
  -webkit-animation: marquee 50s linear -35s infinite;
          animation: marquee 50s linear -35s infinite;
}

.Writing-archive .line-8 {
  -webkit-animation: marquee 50s linear -40s infinite;
          animation: marquee 50s linear -40s infinite;
}

.Writing-archive .line-9 {
  -webkit-animation: marquee 50s linear -45s infinite;
          animation: marquee 50s linear -45s infinite;
}

.Writing-archive:hover div {
  cursor: hover;
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
  z-index: 1000;
}

@-webkit-keyframes marquee {
  0% {
    right: -100%;
  }
  100% {
    right: 100%;
  }
}

@keyframes marquee {
  0% {
    right: -100%;
  }
  100% {
    right: 100%;
  }
}

.Writing-line--title {
  font-weight: 500;
  text-decoration: none;
  font-size: 1rem;
  color: inherit;
}

.Writing-line-container {
  padding-right: 50px;
}

.Writing-line--publish {
  color: rgb(168, 178, 209);
  font-size: 0.9rem;
  margin: 5px 0;
}

.Writing-page-container {
  margin: auto;
  width: 100%;
  display: block;
}

.Writing-thought-container {
  position: absolute;
  left: 250px;
}

.Writing-thought-header {
  font-size: 28px;
  font-family: "Rowdies", cursive;
  margin-bottom: 10px;
}

.Dropdown-summary {
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-weight: 500;
  font-size: 20px;
  font-family: "Rowdies", cursive;

  display: flex;
  align-items: center;
}

.Dropdown {
  width: 100%;
  max-width: 300px;
}

.Dropdown p {
  font-size: 15px;
}

.Dropdown a {
  color: inherit;
}

.Dropdown-content {
  margin-top: 1.2rem;
}

.Dropdown-point-icon {
  margin-left: 0.5em;
}

.DesignNotes--thoughts {
  width: 100%;
  max-width: 300px;

  color: #31708f;
  background-color: #d9edf7;
  border-color: #bce8f1;

  margin: 0 0 0 20%;
  padding: 15px 5%;
  border: 1px solid transparent;
  border-radius: 4px;
}

.DesignNotes-page {
  padding-top: 100px;
  position: relative;
  margin: 0 auto;
}

.About-container {
  padding: 100px 0;
  display: flex;
  flex-wrap: wrap;
}

.Girl-dozing {
  height: 50vh;
}

.Girl-dozing-container {
  display: block;
  padding: 0 40px;
}

.About-dropdown {
  margin: 0;
  padding-left: 25%;
  display: block;
  z-index: 2;
  width: 20rem;
}

.Top-button {
  display: inline-block;
  transition: all 0.2s ease-in-out;
  background: 0 0;
  border: none;
  outline: 0;
  width: 45px;
  position: fixed;
  bottom: 25px;
  right: 30px;
  cursor: pointer;
  z-index: 3;
}

.Top-button img {
  width: 100%;
}

@media screen and (max-width: 601px) {
  .Top-button {
    bottom: 15px;
    right: 3px;
    width: 40px;
  }

  .Top-button img {
    width: 40px;
  }
}

